import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import { ApiType, CoreClient } from '@youfoundation/transit-lib';
import AppManagementProvider from '../../provider/app/AppManagementProvider';
import {
  AppClientRegistrationRequest,
  AppRegistrationRequest,
  DriveGrantRequest,
  PermissionSet,
} from '../../provider/app/AppManagementProviderTypes';
import useAuth from '../auth/useAuth';

const useApp = ({ appId }: { appId: string }) => {
  const queryClient = useQueryClient();
  const { getSharedSecret } = useAuth();
  const appManagementProvider = AppManagementProvider.getInstance(getSharedSecret());
  const client = new CoreClient({ api: ApiType.Owner, sharedSecret: getSharedSecret() });

  const fetch = async ({ appId }: { appId: string }) => {
    return await appManagementProvider.GetAppRegistration({ appId: appId });
  };

  const registerNewApp = async ({
    appId,
    name,
    permissionSet,
    driveGrants,
  }: {
    appId: string;
    name: string;
    permissionSet: PermissionSet;
    driveGrants: DriveGrantRequest[];
  }) => {
    await Promise.all(
      driveGrants.map(async (driveGrant) => {
        return await client.driveProvider.EnsureDrive(
          driveGrant.permissionedDrive.drive,
          driveGrant.driveMeta.name,
          driveGrant.driveMeta.description,
          false
        );
      })
    );

    const appRegRequest: AppRegistrationRequest = {
      appId: appId,
      name: name,
      permissionSet: permissionSet,
      drives: driveGrants.map((driveGrant) => {
        return { ...driveGrant, driveMeta: undefined };
      }),
    };

    await appManagementProvider.RegisterApp(appRegRequest);
  };

  const registerNewAppClient = async ({
    appId,
    clientPublicKey64,
    clientFriendlyName,
  }: {
    appId: string;
    clientPublicKey64: string;
    clientFriendlyName: string;
  }) => {
    const clientRegRequest: AppClientRegistrationRequest = {
      appId: appId,
      clientPublicKey64: clientPublicKey64,
      clientFriendlyName: clientFriendlyName,
    };

    return appManagementProvider.RegisterAppClient(clientRegRequest);
  };

  const revokeApp = async ({ appId }: { appId: string }) => {
    return await appManagementProvider.RevokeApp({ appId: appId });
  };

  const allowApp = async ({ appId }: { appId: string }) => {
    return await appManagementProvider.AllowApp({ appId: appId });
  };

  return {
    fetch: useQuery(['app', appId], () => fetch({ appId }), {
      refetchOnWindowFocus: false,
    }),
    registerNewApp: useMutation(registerNewApp, {
      onSuccess: (data, param) => {
        queryClient.invalidateQueries(['app', param.appId]);
        queryClient.invalidateQueries(['registeredApps']);
      },
      onError: (ex) => {
        console.error(ex);
      },
    }),
    registerNewAppClient: useMutation(registerNewAppClient, {
      onSuccess: (data, param) => {
        queryClient.invalidateQueries(['app', param.appId]);
      },
      onError: (ex) => {
        console.error(ex);
      },
    }),
    revokeApp: useMutation(revokeApp, {
      onSuccess: (data, param) => {
        queryClient.invalidateQueries(['app', param.appId]);
        queryClient.invalidateQueries(['registeredApps']);
      },
      onError: (ex) => {
        console.error(ex);
      },
    }),
    allowApp: useMutation(allowApp, {
      onSuccess: (data, param) => {
        queryClient.invalidateQueries(['app', param.appId]);
        queryClient.invalidateQueries(['registeredApps']);
      },
      onError: (ex) => {
        console.error(ex);
      },
    }),
  };
};

export default useApp;
