import { DriveDefinition } from '@youfoundation/transit-lib';
import { Link } from 'react-router-dom';
import { t } from '../../../helpers/i18n/dictionary';
import useDrive from '../../../hooks/drive/useDrive';
import { DriveGrant } from '../../../provider/connections/CircleDataTypes';
import { drivePermissionLevels } from '../../../provider/permission/permissionLevels';
import Arrow from '../../Icons/Arrow/Arrow';
import HardDrive from '../../Icons/HardDrive/HardDrive';
import LoadingParagraph from '../../Loaders/LoadingParagraph/LoadingParagraph';
import { getAccessFromPermissionNumber } from '../../Templates/DemoData/helpers';

const DrivePermissionView = ({
  driveGrant,
  permissionTree,
  className,
}: {
  driveGrant: DriveGrant;
  permissionTree?: string;
  className?: string;
}) => {
  const { data: drive, isLoading } = useDrive({
    targetDrive: driveGrant.permissionedDrive.drive,
  }).fetch;

  if (isLoading) {
    return <LoadingParagraph className={`h-4 max-w-xs ${className}`} />;
  }

  return (
    <div
      key={`${driveGrant?.permissionedDrive?.drive?.alias}-${driveGrant?.permissionedDrive.drive?.type}`}
      className={`flex flex-row ${className}`}
      title={permissionTree}
    >
      <Link
        to={`/owner/drives/${drive?.targetDriveInfo?.alias}_${drive?.targetDriveInfo?.type}`}
        className="flex flex-row hover:text-slate-700 hover:underline dark:hover:text-slate-400"
      >
        <HardDrive className="mt-1 mb-auto mr-3 h-6 w-6" />
        <div className="mr-2 flex flex-col">
          <p className={`leading-none ${!permissionTree ? 'my-auto' : ''}`}>
            {drive?.name}:{' '}
            {
              getAccessFromPermissionNumber(
                driveGrant?.permissionedDrive.permission,
                drivePermissionLevels
              ).name
            }
          </p>
          {permissionTree && (
            <small className="">
              {t('Via')} {permissionTree}
            </small>
          )}
        </div>
        <Arrow className="my-auto ml-auto h-5 w-5" />
      </Link>
    </div>
  );
};

export const DriveView = ({ drive, className }: { drive: DriveDefinition; className?: string }) => {
  return (
    <div
      key={`${drive.targetDriveInfo?.alias}-${drive.targetDriveInfo?.type}`}
      className={`flex flex-row ${className}`}
    >
      <Link
        to={`/owner/drives/${drive?.targetDriveInfo?.alias}_${drive?.targetDriveInfo?.type}`}
        className="flex flex-row hover:text-slate-700 hover:underline dark:hover:text-slate-400"
      >
        <HardDrive className="mt-1 mb-auto mr-3 h-6 w-6 flex-shrink-0" />
        <div className="mr-2 flex flex-col">
          <p className={`my-auto`}>{drive?.name}</p>
        </div>
        <Arrow className="my-auto ml-auto h-5 w-5" />
      </Link>
    </div>
  );
};

export default DrivePermissionView;
